<template>    
    <div class="container-fluid">
        <div class="d-none d-lg-block bg-poster-image animated fadeIn" :style="poster_css"></div>
        <div class="row my-3">
            <div class="col-12 col-md-10 offset-md-1">
                <div class="row">
                    <div class="col-12 col-xl-9 mt-3 zi10">
                        <!-- Main block -->
                        <div class="row">
                            <div class="col-12 ratio ratio-16x9">
                                <iframe :src="getSrc()" frameborder="0" scrolling="no" allowfullscreen="" referrerpolicy="origin"></iframe>
                            </div>
                            <div class="col-12 px-3 px-md-0">
                                <h4 class="w-75 text-start theme-title text-capitalize mt-3 mb-0 float-start">{{type}} {{title}}</h4>
                                <h4 class="w-25 text-end text-muted mt-3 mb-0 float-end">{{ratingAgeLimits}}</h4>
                            </div>
                            <hr class="w-100 theme-border-white mt-3 mb-3">
                            <div class="col-12 col-lg-6 px-0 text-center text-lg-start">
                                <h4 class="theme-text my-2">Информация к {{type}}у</h4>
                            </div>
                            <div class="col-12 col-lg-6 px-0 text-center text-lg-end mt-3 mt-lg-0">
                                <a href="https://go.iny.su/donate" target="_blank" class="btn btn-outline-info btn-rounded me-3">Донат</a>
                                <button @click="subscriptions" :title="subscriptionCount" :class="['btn', (is_subscriptions ? 'btn-outline-primary':'btn-outline-danger'), 'btn-rounded', 'theme-duration', 'ms-3']">{{text_subscriptions}}</button>
                            </div>
                            <div class="col-12 px-0 my-3">
                                <ul class="list-group list-group-flush rounded">
                                    <li class="list-group-item theme-panel theme-text">
                                        <span class="float-start w-50 text-start theme-title">Год производства</span> 
                                        <span class="float-end w-50 text-start">{{year}}</span>
                                    </li>
                                    <li class="list-group-item theme-panel theme-text">
                                        <span class="float-start w-50 text-start theme-title">Страна</span> 
                                        <span class="float-end w-50 text-start">{{text_countries}}</span>
                                    </li>
                                    <li class="list-group-item theme-panel theme-text">
                                        <span class="float-start w-50 text-start theme-title">Жанр</span> 
                                        <span class="float-end w-50 text-start">{{text_genres}}</span>
                                    </li>
                                    <li  class="list-group-item theme-panel theme-text">
                                        <span class="float-start w-50 text-start theme-title">Слоган</span> 
                                        <span class="float-end w-50 text-start">{{slogan ? slogan:slogan}}</span>
                                    </li>
                                    <li class="list-group-item theme-panel theme-text">
                                        <span class="float-start w-50 text-start theme-title">Время</span> 
                                        <span class="float-end w-50 text-start">{{filmLength}}</span>
                                    </li>
                                    <li class="list-group-item theme-panel theme-text">
                                        <span class="text-start theme-title">Описание:<br>{{description}}</span>
                                    </li>
                                </ul>
                            </div>
                            <div class="col-12 theme-panel rounded text-center py-3 my-3">
                                <h4 class="text-center text-center theme-title py-3 mb-3">Факты</h4>
                                <div class="row">
                                    <h5 v-for="(fact, idx) in facts" :key="'fact' + idx" class="d-block theme-text text-start px-3 mb-3">
                                        <span class="theme-link">#{{idx+1}}</span>
                                        {{fact}}
                                    </h5>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="col-12 col-xl-3 mt-3 zi10">
                        <!-- Recoms block -->
                        <div class="row">
                            <div class="col-12 text-center mb-3 ps-5">
                                <h4 class="my-0 py-3">Рекомендации</h4>
                            </div>
                        </div>
                        <div class="row">
                            <AppCart v-for="watch in watchDataRecoms" :key="watch.filmId" :state="'max-content'" :type="watch.type" :kpid="watch.filmId" :title="watch.nameRu" :year="watch.year" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import AppCart from '../components/AppCart.vue'
const axios = require('axios')
//import {Base64} from '..base64/'

export default {
    name: 'Watch',
    components: {
        AppCart
    },
    
    data: () => {
        return {
            kpid: '',
            type: '',
            title: '',
            poster_css: '',
            year: '',
            ratingAgeLimits: '14+',
            is_subscriptions: false,
            text_subscriptions: '',
            text_countries: '',
            text_genres: '',
            slogan: '',
            filmLength: '',
            description: '',
            facts: [],
            watchDataRecoms: [],
            subscriptionCount: ''
        }
    },
    methods: {
        subscriptions() {
            if (this.is_subscriptions) {
                axios.get(`https://iny.su/api.php?_action=media.subscription&v=0.1&kpid=${this.kpid}&act=unsubscribe&jwt=${this.$store.getters.USER_TOKEN}`)
                this.is_subscriptions = false
                this.text_subscriptions = 'Подписаться'
            } else {
                axios.get(`https://iny.su/api.php?_action=media.subscription&v=0.1&kpid=${this.kpid}&act=subscribe&jwt=${this.$store.getters.USER_TOKEN}`)
                this.is_subscriptions = true
                this.text_subscriptions = 'Отписаться'
            }
        },
        getSrc() {
            return 'https://dud.allohalive.com/?kp=' + this.kpid + '&amp;token=10b16a40f5793e2d02d06265c13912'
        }
    },
    mounted() {
        document.title = 'Watch'
        this.kpid = this.$route.query.kpid
        this.poster_css = 'background-image: url(https://kinopoiskapiunofficial.tech/images/posters/kp/' + this.kpid + '.jpg);'

        // redirect
        if (!this.kpid) this.$router.push(`/main`)
        
        // получаю рекомендации
        axios
            .get('https://iny.su/api/0.1/media/mediaWatchRecoms.json')
            .then(res => {
                this.watchDataRecoms = res.data
            })

        // get data
        if (this.$store.getters.USER_TOKEN) {
            axios
            .get(`https://iny.su/api.php?_action=media.watch&v=0.1&kpid=${this.kpid}&jwt=${this.$store.getters.USER_TOKEN}`)
            .then(res => {
                let watchData = res.data.watchData
                let is_subscription = res.data.is_subscription
                this.subscriptionCount = res.data.subscriptionCount
                if (is_subscription == 'subscribe') {
                    this.is_subscriptions = true
                    this.text_subscriptions = 'Отписаться'
                } else {
                    this.is_subscriptions = false
                    this.text_subscriptions = 'Подписаться'
                }

                switch(watchData.data.type) {
                    case 'FILM':
                    case 'UNKNOWN':
                        this.type = 'фильм'
                        break
                    case 'TV_SHOW':
                        this.type = 'сериал'
                        break
                }
                document.title = watchData.data.nameRu + ', ' + watchData.data.year
                this.title = watchData.data.nameRu || 'Название'
                this.year = watchData.data.year || 'Название'
                
                let countries = []
                watchData.data.countries.forEach(element => {
                    countries.push(element.country)
                })
                this.text_countries =  countries.join(', ')

                let genres = []
                watchData.data.genres.forEach(element => {
                    genres.push(element.genre)
                })
                this.text_genres = genres.join(', ')

                this.slogan = watchData.data.slogan || '-'
                this.filmLength = watchData.data.filmLength || 'Продолжительность не указана'
                this.description = watchData.data.description || 'Описание не добавлено'
                this.facts = watchData.data.facts || ['Факты еще не подобраны']
                this.ratingAgeLimits = (watchData.data.ratingAgeLimits + '+') || '14+'
            })
        }
        else {
            this.$router.push(`/auth?to=/watch?kpid=${this.kpid}`)
        }
        
    }
}
</script>

<style>
.ratio {
    background-color: var(--theme-panel);
}
.bg-poster-image {
    width: 100vw;
    height: 100vh;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    position: absolute;
    z-index: 0;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    filter: brightness(0.3) blur(6px);
    transition-duration: 1s;
}
.bg-poster-image:after {
    content: '';
    width: 100vw;
    height: 100vh;
    top: 0px;
    left: 0px;
    bottom: 0px;
    right: 0px;
    position: absolute;
    z-index: -5;
    background: linear-gradient(180deg, rgba(0,0,0,0) 0%, #505050 94%)!important;
}
.zi10 {
    z-index: 10;
}
</style>